.derecha {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .derecha {
      align-items: flex-end; }
      .derecha .navEffect {
        display: flex;
        justify-content: center;
        align-items: center; }
      .derecha .navEffect .navEffect-a {
        position: relative;
        display: inline-block;
        background: #fff; }
      .derecha .navEffect .navEffect-a:before,
      .derecha .navEffect .navEffect-a:after {
        content: '';
        position: absolute;
        inset: 0;
        background: #f00;
        transition: 0.5; }
      .derecha .navEffect .navEffect-a:nth-child(1):before,
      .derecha .navEffect .navEffect-a:nth-child(1):after {
        background: linear-gradient(45deg, #00ccff, #0e1538, #0e1538, #d400d4); }
      .derecha .navEffect .navEffect-a:nth-child(2):before,
      .derecha .navEffect .navEffect-a:nth-child(2):after {
        background: linear-gradient(45deg, #ff0080, #0e1538, #0e1538, #00d412); }
      .derecha .navEffect .navEffect-a:nth-child(3):before,
      .derecha .navEffect .navEffect-a:nth-child(3):after {
        background: linear-gradient(45deg, #ffe600, #0e1538, #0e1538, #00d4d4); }
      .derecha .navEffect .navEffect-a:nth-child(4):before,
      .derecha .navEffect .navEffect-a:nth-child(4):after {
        background: linear-gradient(45deg, #ff0037, #0e1538, #0e1538, #00d4c2); }
      .derecha .navEffect .navEffect-a:nth-child(5):before,
      .derecha .navEffect .navEffect-a:nth-child(5):after {
        background: linear-gradient(45deg, #2f00ff, #0e1538, #0e1538, #d40075); }
      .derecha .navEffect .navEffect-a:hover:before {
        inset: -3px; }
      .derecha .navEffect .navEffect-a:hover:after {
        inset: -3px;
        filter: blur(10px); }
      .derecha .navEffect .navEffect-a span {
        position: relative;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: inline-block;
        background: #333333;
